@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
  background-image: linear-gradient(
    to right,
    transparent,
    rgb(130, 130, 130),
    darkgrey,
    transparent
  );
  display: grid;
  grid-template-rows: 90px 30px;
  opacity: 0.9;
}

.header-logo {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  font-family: "Permanent Marker", cursive;
  font-size: 1.8rem;
  -webkit-justify-content: center;
          justify-content: center;
}

.bball-img {
  height: 55px;
  position: relative;
  top: 8px;
}

.header-nav {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  font-family: "Roboto", sans-serif;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: auto;
  width: 500px;
}

.header-nav-link {
  cursor: pointer;
  margin: 0;
  padding: 5px;
  text-decoration: none;
  transition: all 0.5s ease-out;
}

.header-nav-link:hover {
  color: white;
  transition: all 0.5s ease-out;
}

@media screen and (max-width: 760px) {
  .Header {
    background: darkgrey;
  }

  .header-logo {
    font-size: 1.35rem;
  }

  .bball-img {
    height: 50px;
    position: relative;
    top: 7px;
  }

  .header-nav {
    width: 350px;
  }
}

@media screen and (max-width: 500px) {
  .header-logo {
    font-size: 1.1rem;
  }

  .bball-img {
    height: 30px;
    position: relative;
    top: 3px;
  }

  .header-nav {
    width: 300px;
  }
}

@media screen and (max-width: 400px) {
  .Header {
    grid-template-rows: 60px 30px;
  }

  .header-logo {
    font-size: 0.7rem;
  }

  .bball-img {
    height: 25px;
    position: relative;
    top: 3px;
  }

  .header-nav {
    width: 280px;
  }

  .header-nav-link {
    font-size: 14px;
  }
}

.PlayerInfo {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(147, 147, 147);
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Roboto", sans-serif;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 70vh;
  margin: 50px 0;
  min-height: 700px;
  opacity: 0.96;
  padding: 0 5%;
  position: relative;
  width: 50vw;
}

.player-info-name {
  font-size: 36px;
}

.player-info-pic {
  height: 200px;
  margin-bottom: 10px;
  opacity: 1.3;
}

.player-info-teams,
.player-info-totals {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 100px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 5px;
  width: 100%;
  font-size: 28px;
}

.player-info-totals {
  line-height: 1.5;
  min-height: 100px;
  text-align: center;
  width: 80%;
}

.player-info-teams {
  min-height: 60px;
}

.player-info-num {
  font-family: "Permanent Marker", cursive;
  font-size: 50px;
}

.player-info-bonus {
  background: grey;
  margin-bottom: 30px;
  min-height: 200px;
  overflow: auto;
  text-align: center;
  width: 100%;
}

.player-info-bonus-title {
  font-family: "Permanent Marker", cursive;
  font-size: 40px;
  margin: 0;
}

.eliminated {
  color: rgb(160, 0, 0);
  text-decoration: line-through;
}

.back-btn {
  background: rgb(84, 84, 84);
  border: none;
  color: white;
  cursor: pointer;
  font-size: 36px;
  min-height: 50px;
  padding: 10px 20px;
  position: absolute;
  right: 0px;
  transition: all 0.5s ease-out;
}

.back-btn:hover {
  background: rgb(227, 227, 227);
  color: rgb(84, 84, 84);
  transition: all 0.5s ease-out;
}

@media screen and (max-width: 1300px) {
  .player-info-teams,
  .player-info-totals {
    font-size: 22px;
  }
}

@media screen and (max-width: 1000px) {
  .PlayerInfo {
    height: auto;
    width: 70vw;
  }

  .player-info-teams {
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 140px;
  }

  .player-info-teams h5 {
    margin: 0;
  }

  .player-info-num {
    font-size: 40px;
  }
}

@media screen and (max-width: 500px) {
  .PlayerInfo {
    width: 80vw;
  }

  .player-info-teams,
  .player-info-totals {
    font-size: 18px;
  }

  .player-info-num {
    font-size: 30px;
  }

  .back-btn {
    font-size: 22px;
  }

  .player-info-bonus {
    font-size: 12px;
  }
  
  .player-info-bonus-title {
    font-size: 30px;
  }
}

.Player {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(147, 147, 147);
  color: white;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  font-family: "Roboto", sans-serif;
  font-size: 30px;
  height: 80px;
  margin: 10px 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 20px;
  transition: all 0.5s ease-in-out;
}

.Player:hover {
  background: rgb(84, 84, 84);
  transition: all 0.5s ease-in-out;
}

.player-pic-wrapper {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 80px;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80px;
}

.player-pic {
  max-height: 80px;
  max-width: 80px;
}

.player-name {
  width: 100px;
}

.player-rank {
  width: 60px;
}

@media screen and (max-width: 900px) {
  .Player {
    font-size: 22px;
  }
}

@media screen and (max-width: 760px) {
  .Player {
    font-size: 18px;
  }
}

@media screen and (max-width: 500px) {
  .Player {
    font-size: 16px;
    height: 60px;
  }
  .player-pic {
    height: 60px;
  }
}

.Standings {
  background: silver;
  height: 75vh;
  opacity: 0.96;
  overflow: auto;
  padding: 0 5%;
  width: 60vw;
}

.current-standings-header {
  -webkit-align-items: center;
          align-items: center;
  background: rgb(84, 84, 84);
  color: white;
  display: -webkit-flex;
  display: flex;
  font-size: 22px;
  height: 50px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
  padding-left: 12px;
  padding-right: 32.5%;
}

.current-standings-points {
  position: relative;
