  }

  .current-standings-points {
    left: 10px;
  }
}

@media screen and (max-width: 500px) {
  .Standings {
    height: auto;
    margin: 20px 0;
    width: 80vw;
  }

  .current-standings-points {
    left: 30px;
  }
}

.Teams {
  opacity: 0.9;
  text-align: center;
}

.teams-wrapper {
  background: silver;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 40px auto;
